.p-temp {
  &__section {
    padding-top: 20px;
  }
  &__title {
    margin-top: 1em;
    @include fz(24);

    color: #ee9c6a;
    border-bottom: 2px solid #ee9c6a;
  }
  &__titleS {
    margin-top: 1em;
    @include fz(20);

    border-bottom: 2px solid #000;
  }
  &__text {
    margin-top: 20px;
    margin-bottom: 50px;
    @include fz(16);

    line-height: 1.7;
  }
  &__code {
    position: relative;
    padding: 20px;
    margin-top: 60px;
    margin-bottom: 100px;
    background: #fff;
